import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { QuizSample } from '../../../SampleData/SampleData';
import AccordionContainer from './AccordionContainer';
import axios from 'axios';
import { FailAlert, SuccessAlert } from '../../../Alert/Alert';

function Quiz(props) {
    const [quizs, setQuizs] = useState([]);
    useEffect(() => {
        FetchQuiz();
    }, []);
    const FetchQuiz = () => {
        axios
            .get('/quizzes/')
            .then((res) => {
                if (res.statusText !== 'OK') {
                    console.log(res);
                    return;
                }
                setQuizs(res.data.results);
            })
            .catch((err) => {
                if (err.response.data.msg) {
                    FailAlert(err.response.data.msg);
                } else {
                    console.log(' : ', err.response);
                }
            });
    };
    return (
        <Container id="quizes">
            <Content>
                <div style={{ padding: '0 0.8rem', wordBreak: 'keep-all' }}>
                    <h1> QUIZ</h1>
                    <p>
                              
                                
                           IQ  . <br />
                               
                          .
                    </p>
                </div>
                {quizs?.map((quiz, index) => (
                    <AccordionContainer quiz={quiz} key={quiz.id} />
                ))}
            </Content>
        </Container>
    );
}
const Container = styled.div`
    width: 100%;
    margin-bottom: 5rem;
    margin-top: 2.5rem;
`;
const Content = styled.div`
    max-width: 1250px;
    margin: auto;
`;
export default Quiz;
